@import url(https://fonts.googleapis.com/css?family=Teko:400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,400i,700&display=swap);
.user-image {
    width: 90px;
    height: 90px;
    background-color: #e9e9e9;
    border-radius: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .user-photo {
    border-radius: 45px;
    width: 80px;
  }
  
  .user-testamonial {
    font-size: large;
  }
  .main-slider{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .slide-content{
    margin-left: 50px;
    margin-right: 50px;
  }
  .sek{
    background-image: url("https://html.rovadex.com/html-creto/assets/img/bg-feedback.jpg");
  
  }
  .clients-cover{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .client{
    padding-top: 100px;
  }
  .subs-cont{
    background-image: url("https://html.rovadex.com/html-creto/assets/img/bg-subscribe.jpg");

  }
  .subs-cont2{
    background-image: url("https://html.rovadex.com/html-creto/assets/img/subscribe-effect.svg");

  }
.s-main-slider:before {
  position: relative  !important;
}
.s-subscribe:before{
  position: relative  !important;

}
  /*# sourceMappingURL=slider.css.map */
.hero-slide {
    height: 500px !important;
    display: flex;
    position: relative;
    overflow: hidden;
  }
  .slide-image {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .hero-slider .content {
    position: relative;
    z-index: 2;
  }
  .color-slider{
    color: #ffd910;
  }
